<template>
  <div class="player-wrapper">
    <div class="container">
      <div class="header">
        <player-header></player-header>
      </div>
      <div
         class="main"
         @mouseenter="onMouseEnter"
         @mouseleave="onMouseLeave"
      >
        <player-control
          :switchStatus="switchStatus"
          :isShowControl="isShowControl"
          :status="status"
          :hideSwitchBtn="hideSwitchBtn"
          @switch="onSwitch"
          @open="onOpen"
          @bespread="onBespread"
        >
          <player-player
            v-show="isShowPlayer"
            :size="playerSize"
            :isShowClose="isShowControl"
            @close="onClose"
          ></player-player>
          <player-drawpanel
            v-show="isShowDrawPanel"
            :size="drawPanelSize"
            :isShowClose="isShowControl"
            @close="onClose"
          ></player-drawpanel>
        </player-control>
      </div>
      <div class="footer">
        <player-footer></player-footer>
      </div>
    </div>
  </div>
</template>

<script>
/**
 * 
 * */
import PlayerPlayer from './components/PlayerPlayer'
import PlayerDrawpanel from './components/PlayerDrawpanel'
import PlayerControl from './components/PlayerControl'
import HuodeScene from 'common/websdk/live'
import FlashTip from 'common/flashtip'
import { mapState } from 'vuex'

const PlayerHeader = () => ({
  component: import('./components/PlayerHeader')
})
const PlayerFooter = () => ({
  component: import('./components/PlayerFooter')
})

export default {
  name: 'Player',
  components: {
    PlayerHeader,
    PlayerPlayer,
    PlayerDrawpanel,
    PlayerControl,
    PlayerFooter
  },
  data () {
    return {
      isShowControl: false,
      switchStatus: true, // flashfalse  true 
      status: true, // 
      isExsitPanel: true, // 
      hideSwitchBtn: true // 
    }
  },
  computed: {
    ...mapState(['template']),
    playerSize () {
      return this.switchStatus ? 'large' : 'small'
    },
    drawPanelSize () {
      return !this.switchStatus ? 'large' : 'small'
    },
    isShowPlayer () {
      if (this.playerSize === 'small') {
        return this.status
      } else {
        return true
      }
    },
    isShowDrawPanel () {
      if (this.drawPanelSize === 'small') {
        return this.status
      } else {
        return true
      }
    }
  },
  watch: {
    template (tem) {
      /*
      *  1 
      *  2 
      *  3 
      *  4 
      *  5 
      *  6 
      * */

      if (tem.type !== 4 && tem.type !== 5) {
        this.hideSwitchBtn = false // 
        this.isExsitPanel = false// 
        this.switchStatus = true // 
        this.status = false // 
      }
    }
  },
  methods: {
    init () {
      const HD = new HuodeScene()

      FlashTip.init()

      HD.onPlayerLoad(() => {
        HD.showControl(false)
        HD.docAdapt(true)
        /*
        * flash google
        * flash,
        * 
        * */
        if (this.isExsitPanel) {
          this.switchStatus = false // 
        }
      })
    },
    onMouseEnter () {
      this.isShowControl = true
    },
    onMouseLeave () {
      this.isShowControl = false
    },
    onSwitch (status) {
      this.switchStatus = status
    },
    onClose (status) {
      this.status = status
    },
    onOpen (status) {
      this.status = status
    },
    onBespread (status) {
      this.$emit('bespread', status)
    }
  },
  mounted () {
    this.init()
  }
}
</script>

<style lang="stylus" scoped>
  @import "~styles/mixins.styl"

  .player-wrapper
    wrapper()
  .container
    width-height-full()
    position relative
    .header
      position absolute
      top 0
      height 70px
      width 100%
    .main
      layout-full(70px, 0, 80px, 0)
    .footer
      position absolute
      height 80px
      width 100%
      bottom 0
</style>



